<template>
  <div class = "container">
    <h1>Full Stack Help Requests</h1>
    <h3>Enter your issue so that it can be seen by a TA!</h3>
    <div class="create-post">
      <v-form>
        <div class = "give-input">
          <v-col>
            <label for="add-name">Your name: </label>
            <input type = "name" id = "add-name" v-model = "name" placeholder = "My name is ...">
          </v-col>
          <v-col>
            <label for="add-problem">Your problem: </label>
            <input type="text" id="add-problem" v-model="text" placeholder = "I'm having trouble with...">
          </v-col>
          
          <v-btn class="submit" v-on:click = "createPost">Submit</v-btn>
          <v-spacer></v-spacer>
      </div>
      

      </v-form>
    </div>
    
    <hr>
    <p class = "error" v-if="error">{{ error }}</p>
    <div class = "posts-container">
      
      <div class = "post"
        v-for="(post,index) in posts" 
        v-bind:item="post" 
        v-bind:index = "index" 
        v-bind:key="post._id" 
        v-on:dblclick="deletePost(post._id)"
        >
        <v-card class = "card">
        <v-card-title>
          <v-toolbar-title class = "name">{{post.name}}</v-toolbar-title>
        </v-card-title>
        <v-card-subtitle>
          <p class = "text" id = "date">{{ `${post.createdAt.getMonth()}/${post.createdAt.getDate()}/${post.createdAt.getFullYear()}` }}</p>
        </v-card-subtitle>
        <v-card-text id = "submission">
          <p class = "text" id = "problem">{{ post.text }}</p>
        </v-card-text>
        <v-btn class = "resolve" v-on:click = "deletePost(post._id)">Resolve</v-btn>
        </v-card>
        
      </div>
    </div>
  </div>
</template>

<script>
import PostService from '../PostService';

import toastr from 'toastr';

export default {
  name: 'PostComponent',
  data() {
    return{
      posts: [],
      error: '',
      name:'',
      text: ''

    }
  },
  async created() {
    try {
      this.posts = await PostService.getPosts();
    } catch(err) {
      this.error = err.message;
    }
  },
  methods: {
    async createPost(){
      if(this.name===''){
        toastr.error("You must enter your name to receive assistance!");
      }
      else if(this.text ===''){
        toastr.error("You must enter a description of the problem to receive assistance!");
      }
      else {
        await PostService.insertPost(this.name, this.text);
        this.posts = await PostService.getPosts();
        toastr.success("Post successfully created!");
      }
    },
    async deletePost(id) {
      await PostService.deletePost(id);
      this.posts = await PostService.getPosts();
      toastr.success("Post sucessfully resolved!");
    }
    
  }
}
</script>

